.resultView {
  min-height: 100vh;
  padding: 5% 0;
  background-size: cover;
  background-repeat: repeat;
  background-position: center;

  .resultView__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    height: 92%;
    overflow-x: hidden;
    overflow-y: auto;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.55);
    color: white;

    @media (min-width: 1024px) {
      max-width: 50%;
    }

    @media (min-width: 1280px) {
      max-width: 42%;
    }

    @media (min-width: 1600px) {
      max-width: 35%;
    }

    .resultView__townName {
      margin-top: 10px;
      padding: 5px;
      font-size: 26px;
      text-transform: uppercase;
      text-align: center;

      @media (min-width: 411px) {
        font-size: 32px;
      }

      @media (min-width: 768px) {
        font-size: 42px;
      }
    }

    .resultView__wrapperTemp {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;

      .resultView__temp {
        font-size: 33px;

        @media (min-width: 411px) {
          font-size: 35px;
        }

        @media (min-width: 768px) {
          font-size: 45px;
        }
      }

      .resultView__maxMinTemp {
        font-size: 16px;

        @media (min-width: 411px) {
          font-size: 17px;
        }

        @media (min-width: 768px) {
          font-size: 21px;
        }
      }
    }

    .resultView__weatherIcon {
      width: 75px;
      margin: 10px 0;
      background-color: rgba(255, 255, 255, 0.4);
      border-radius: 50%;

      @media (min-width: 411px) {
        width: 85px;
      }

      @media (min-width: 768px) {
        width: 100px;
      }
    }

    .resultView__wrapperInfo {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;

      .resultView__infoText {
        font-size: 16px;

        @media (min-width: 411px) {
          font-size: 17px;
        }

        @media (min-width: 768px) {
          font-size: 23px;
        }

        .resultView__info {
          font-weight: bold;
        }
      }
    }

    .resultView__wrapperIcons {
      display: flex;
      justify-content: space-evenly;
      width: 100%;
      margin: 35px 0 5px;
    }

    .resultView__dateOfRefresh {
      margin: 10px 0 0;
      font-size: 15px;

      @media (min-width: 411px) {
        font-size: 16px;
      }

      @media (min-width: 768px) {
        font-size: 20px;
      }

      @media (min-width: 768px) {
        font-size: 18px;
      }
    }

    .resultView__mainTown {
      margin: 0 0 15px;
      font-size: 15px;

      @media (min-width: 411px) {
        font-size: 16px;
      }

      @media (min-width: 768px) {
        font-size: 20px;
      }

      @media (min-width: 768px) {
        font-size: 18px;
      }

      .resultView__info {
        font-weight: bold;
      }
    }
  }
}

@media (min-width: 768px) {
  ::-webkit-scrollbar {
    width: 10px;
  }

  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px #aaa;
    border-radius: 10px;
    margin: 10px;
  }

  ::-webkit-scrollbar-thumb {
    background-color: rgba(192, 192, 192, 0.5);
    border-radius: 10px;
  }
}
